import React, { Component } from 'react';
import {Container, Row, Col, Navbar, Nav, Image} from 'react-bootstrap';
import AppNav from '../../frame/nav';

export default class VerticalNav extends AppNav {
  constructor(props) {
    super(props);
    this.state = {
      expand: false
    }
  }

  collapseMenuAndScrollTo = (section) => {
    this.setState({ expand: false });
    this.ScrollToSection(section);
  }

  render() {
    return(
      <Row id="horizontalNav" className={`w-100 m-0 ${this.props.show ? "visible" : "invisible"}`} style={{background: 'rgba(256, 256, 256, 0.9)', boxShadow: '0 5px 6px -6px rgba(0,0,0,.5)'}}>
        <Navbar 
          expand="sm" bg="light" variant="light" fixed='top' 
          expanded={this.state.expand}
          onToggle={() => {this.setState({expand: !this.state.expand})} }
          className="px-4 py-0 w-100 shadow"
        >
          <Navbar.Brand onClick={() => this.ScrollToSection('title')} style={{cursor: 'pointer'}}>My</Navbar.Brand>
          <Navbar.Toggle aria-controls="horizontalNav-control" />
          <Navbar.Collapse id="horizontalNav-control">
            <Nav className="mr-auto">
              <Nav.Link onClick={() => this.collapseMenuAndScrollTo('mybackground')}>Background</Nav.Link>
              <Nav.Link onClick={() => this.collapseMenuAndScrollTo('myprojects')}>Projects</Nav.Link>
              <Nav.Link onClick={() => this.collapseMenuAndScrollTo('myachievements')}>Achievements</Nav.Link>
              <Nav.Link onClick={() => this.collapseMenuAndScrollTo('mycontact')}>Contact</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
      </Row>
    );
  }
}