import React, { Component } from 'react';
import {Container, Row, Col, Navbar, Nav, Image} from 'react-bootstrap';
import AppNav from '../../frame/nav';
import propic from '../../image/propic_1x1.jpg';

export default class VerticalNav extends AppNav {
  render() {
    return(
      <Navbar className="d-flex flex-column h-100 justify-content-start py-sm-2 py-4">
        <Nav className="d-flex flex-column align-items-center justify-content-start h-100">
          <Nav.Item className="px-lg-5 px-sm-3 px-5 bg-transparent">
            <Image src={propic} roundedCircle thumbnail className="ratio ratio-1x1"/>
          </Nav.Item>
          <Nav.Link className="text-dark fs-4 nav-btn pt-3 pb-1 text-center" onClick={() => this.ScrollToSection('title')}>
            Ryan Chung
          </Nav.Link>
          <Nav.Link className="nav-btn pt-3 pb-1 text-center" onClick={() => this.ScrollToSection('mybackground')}>
            Background
          </Nav.Link>
          <Nav.Link className="nav-btn pt-3 pb-1 text-center" onClick={() => this.ScrollToSection('myprojects')}>
            Projects
          </Nav.Link>
          <Nav.Link className="nav-btn pt-3 pb-1 text-center" onClick={() => this.ScrollToSection('myachievements')}>
            Achievements
          </Nav.Link>
          <Nav.Link className="nav-btn pt-3 pb-1 text-center" onClick={() => this.ScrollToSection('mycontact')}>
            Contact
          </Nav.Link>
        </Nav>
      </Navbar>
    );
  }
}