import React, { Component } from 'react';
import {Row, Col, Image} from 'react-bootstrap';

export default class Section extends Component {
    constructor(props) {
      super(props);
    }
  
    render() {
      let title = this.props.name
        ? <Row className="font-weight-bold mb-4 mx-0" style={{fontSize: 24}}>{this.props.name}</Row>
        : <></>;
      let bgImage = this.props.bg
        ? <Row className="position-relative mx-0" style={{top: 0, left: 0, zIndex: -1, opacity: this.props.opacity}}><Image src={this.props.bg} height="100%"/></Row>
        : <></>;
      let content = this.props.children
        ? <Row className="justify-content-start mx-0" style={{textAlign: 'justify'}}>{this.props.children}</Row>
        : <></>;

      return(
        <>
        <Row id={this.props.id} className="min-vh-100 p-lg-5 py-5 px-4 overflow-hidden w-100 m-0">
          <Col className="w-100 text-left px-lg-4 px-md-3 px-0">
            {title}
            {content}
          </Col>
        </Row>
        <hr className="mx-sm-5 mx-4"/>
        </>
      );
    }
}