import React, { Component } from 'react';
import Section from '../frame/section'
import cs from '../image/cs.jpg';
import introduction from '../data/introduction.json';

export default class MyBackground extends Component {
    render() {
      return(
        <Section id="mybackground" name="Background" bg={cs} opacity="0.1">

          {
            introduction.body.map((paragraph, index) => {
              return <p key={index} className="p-0">{paragraph}</p>;
            })
          }
        </Section>
      );
    }
}
  